<template>
  <!-- Daito 大东-->
  <div class="daito">
    <!-- PC端，响应为app端时自动隐藏 -->
    <div class="hidden-sm-and-down">
      <div class="peopleContainer">
        <h1>{{$t('heroes.daito')}}</h1>
        <p>{{$t('heroes.daito1')}}</p>
        <div>
          <div>
            <img src="../../assets/img/04.jpg" alt/>
          </div>
          <div>
            <h2>{{$t('heroes.daito2')}}</h2>
            <p>{{$t('heroes.scan')}}</p>
            <div>
              <img src="../../assets/img/daito.png" alt />
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- APP端，响应为PC端时自动隐藏 -->
    <div class="hidden-sm-and-up">
      <div class="people-content">
        <div class="people-title">
          <h1>{{$t('heroes.daito')}}</h1>
          <p>{{$t('heroes.daito1')}}</p>
        </div>
        <div class="people-img">
          <el-row>
            <el-col :span="12">
              <img src="../../assets/img/04.jpg" alt style="height: 213px; width: 150px;"/>
            </el-col>
            <el-col :span="12">
              <h5>{{$t('heroes.daito2')}}</h5>
              <p>{{$t('heroes.scan')}}</p>
              <img src="../../assets/img/daito.png" alt style="height: 102px; width: 105px" />
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Daito'
}
</script>

<style lang="scss" scoped>
.daito {
@import "../../assets/css/people.scss";
}

</style>



